body {
  font-size: 16px;
  line-height: normal;
  cursor: default;
  color: #252644;
  font-family: "Montserrat", sans-serif;
}

a {
  cursor: pointer;
  text-decoration: none;
  transition: all 0.25s ease-in-out;
}
a:hover {
  text-decoration: none;
}

.button {
  display: inline-block;
  box-sizing: border-box;
  transition: all ease 250ms;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  line-height: normal;
  border: none;
  background: none;
  transition: all 0.25s ease-in-out;
}
.button:hover {
  text-decoration: none;
}
.button.button-login {
  padding: 14px 48px;
  border: 1px solid #252644;
  box-sizing: border-box;
  border-radius: 5px;
}
.button.button-login span {
  position: relative;
  padding-left: 23px;
  font-size: 16px;
  line-height: normal;
}
.button.button-login span::before {
  content: " ";
  position: absolute;
  width: 14px;
  height: 13px;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.button.button-login.dark {
  border-color: #252644;
}
.button.button-login.dark span {
  color: #252644;
}
.button.button-login.dark span::before {
  background-image: url(/img/icons/icon-login_dark.svg);
}
.button.button-login.light {
  border-color: #fff;
}
.button.button-login.light span {
  color: #fff;
}
.button.button-login.light span::before {
  background-image: url(/img/icons/icon-login_light.svg);
}
.button.primary {
  border-radius: 5px;
  background: #01B5E8;
  border: 1px solid #01B5E8;
  font-weight: bold;
  font-size: 16px;
  line-height: normal;
  text-align: center;
  padding: 15px 70px;
  color: #FFFFFF;
}
.button.secondary {
  padding: 19px 45px;
  font-weight: bold;
  font-size: 16px;
  line-height: normal;
  text-align: center;
  color: #01B5E8;
  border: 2px solid #7BD5FA;
  box-sizing: border-box;
  border-radius: 5px;
}
.button.dark-outline {
  padding: 17px 52px;
  font-weight: bold;
  font-size: 15px;
  line-height: normal;
  text-align: center;
  color: #252644;
  border: 2px solid #252644;
  box-sizing: border-box;
  border-radius: 5px;
}
.button.dark-outline:hover {
  background: #252644;
  color: #fff;
}
.button.big {
  width: 100%;
  height: 72px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button.button-to-change {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  text-decoration-line: underline;
  color: #989898;
}
.button.button-to-change:hover {
  color: #7f7f7f;
}
@media (min-width: 768px) and (max-width: 991.98px) {
  .button.button-login {
    padding: 13px 35px;
  }
  .button.button-login span {
    padding-left: 23px;
    font-size: 14px;
  }
}
@media (max-width: 767.98px) {
  .button.big {
    height: 50px;
  }
  .button.button-login {
    padding: 10px 30px;
  }
  .button.button-login span {
    padding-left: 23px;
    font-size: 14px;
  }
}

.link {
  cursor: pointer;
  display: inline-block;
}
.link.with-arrow span {
  font-weight: bold;
  font-size: 15px;
  line-height: 1.2em;
  text-align: center;
  transition: all ease 250ms;
  color: #01B5E8;
  position: relative;
  padding-right: 50px;
}
.link.with-arrow span::before {
  content: " ";
  position: absolute;
  width: 34px;
  height: 10px;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background: no-repeat center/contain url(/img/icons/icon-arrow_blue.svg);
  transition: all ease 250ms;
}
.link.with-arrow:hover span {
  color: #01a1cf;
}
.link.with-arrow:hover span::before {
  right: -6px;
}
.link-to-report {
  transition: all ease 280ms;
}
.link-to-report span {
  font-weight: 600;
  font-size: 13px;
  line-height: 1.2em;
  text-align: center;
  color: #252644;
  position: relative;
  transition: all ease 280ms;
  padding-left: 23px;
}
.link-to-report span::before {
  transition: all ease 280ms;
  content: " ";
  position: absolute;
  left: 0;
  top: 50%;
  width: 14px;
  height: 14px;
  transform: translateY(-50%);
  background: no-repeat center/contain url(/img/icons/icon-error.svg);
}
