.custom-select {
  all: inherit;
  position: relative;
}
.custom-select select {
  display: none;
}
.custom-select-trigger {
  background: #fff;
  position: relative;
  display: block;
  width: 100%;
  height: 72px;
  line-height: 40px;
  padding: 0 50px 0 15px;
  font-weight: 500;
  color: #01B5E8;
  font-size: 16px;
  color: #7A7A7A;
  cursor: pointer;
  transition: all ease 250ms;
  line-height: normal;
  display: flex;
  align-items: center;
}
.custom-select-trigger:after {
  position: absolute;
  display: block;
  content: "";
  width: 10px;
  height: 10px;
  top: 50%;
  right: 15px;
  margin-top: -3px;
  border-bottom: 2px solid #01B5E8;
  border-right: 2px solid #01B5E8;
  transform: rotate(45deg) translateY(-50%);
  transition: all 0.4s ease-in-out;
  transform-origin: 50% 0;
}
.custom-select.opened .custom-select-trigger {
  border-radius: 5px 5px 0 0;
}
.custom-select.opened .custom-select-trigger:after {
  top: 55%;
  transform: rotate(-135deg) translateY(-70%);
}
.custom-select.opened .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}
.custom-select .custom-options {
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  min-width: 100%;
  margin: 0 0;
  background: #fff;
  transition: all 0.2s ease-in-out;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-15px);
  z-index: 2;
  border: 1px solid #DDDDDD;
  box-sizing: border-box;
  border-top: 0;
  border-radius: 1px 1px 5px 5px;
  box-shadow: 0px 0px 10px rgba(221, 221, 221, 0.25);
}
.custom-select .custom-options .custom-option {
  display: block;
  padding: 13px 22px;
  color: #444444;
  line-height: 1.2em;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
  margin-bottom: 5px;
  font-size: 14px;
  margin-bottom: 1px;
}
.custom-select .custom-options .custom-option:last-child {
  border-radius: 0 0 5px 5px;
  margin-bottom: 0;
}
.custom-select .custom-options .custom-option:hover {
  background: #FDF9ED;
}
.custom-select.opened .custom-select-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}

.custom-select-wrapper select {
  display: none;
}

